	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Snowstreet","date":"05 Jan 2003","slug":"/2003-01-05-snowstreet/","tags":"Y2003"},"html":"<p><img alt=\"snowstreet.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/01/snowstreet.jpg\" width=\"325\" height=\"244\" border=\"0\" /></p>\n<p>Our steet, cobblestones covered with snow.</p>"},"next":{"fields":{"title":"Let It Snow","date":"05 Jan 2003","slug":"/2003-01-05-let-it-snow/"}},"previous":{"fields":{"title":"Colder Than It Needs To Be","date":"08 Jan 2003","slug":"/2003-01-08-colder-than-it-needs-to-be/"}}},{"node":{"fields":{"title":"Let It Snow","date":"05 Jan 2003","slug":"/2003-01-05-let-it-snow/","tags":"Y2003"},"html":"<p>Christopher insists that the fun of having a sled is <em>pulling</em> it, not riding it.</p>\n<p><img alt=\"crhsnow1.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/01/crhsnow1.jpg\" width=\"325\" height=\"244\" border=\"0\" /></p>\n<p><img alt=\"crhsled1.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/01/crhsled1.jpg\" width=\"325\" height=\"244\" border=\"0\" /></p>"},"next":{"fields":{"title":"Joining The Gigahertz Generation","date":"05 Jan 2003","slug":"/2003-01-05-joining-the-gigahertz-generation/"}},"previous":{"fields":{"title":"Snowstreet","date":"05 Jan 2003","slug":"/2003-01-05-snowstreet/"}}},{"node":{"fields":{"title":"Joining The Gigahertz Generation","date":"05 Jan 2003","slug":"/2003-01-05-joining-the-gigahertz-generation/","tags":"Y2003"},"html":"<p>After two years of working at 900 MHz, I finally went out and bought a new computer. Actually, I didn't, I haven't bought a whole new computer in over 10 years. I only bought a new CPU and motherboard, and the requisite DDR RAM. The RAM cost as much as the CPU and motherboard combined, and was the real reason I waited longer than usual before upgrading. I did follow my usual strategy of a getting a low-end AMD processor that doubles my current speed. Well, at least the marketing speed, since the 1800+ runs at 1533 MHz. I can now build world under FreeBSD in 40 minutes.</p>\n<p>I did think about getting a Mac, especially now that I've got a iMac at work. OSX is nice. Too bad it only runs on overpriced proprietary Apple hardware.  My OS isn't beholden to an evil monopoly, and my hardware shouldn't be either. I'm sure Steve Jobs would brainwash me to want an entire new Mac every couple of years. He wouldn't let me get by with just a CPU and motherboard. And I could could never push a Mac past the Domestic Financial Planning and Review Council.</p>"},"next":{"fields":{"title":"Cool and Works Nicely","date":"03 Jan 2003","slug":"/2003-01-03-cool-and-works-nicely/"}},"previous":{"fields":{"title":"Let It Snow","date":"05 Jan 2003","slug":"/2003-01-05-let-it-snow/"}}}],"pathPrefix":"","first":false,"last":false,"index":798,"pageCount":1101}}

